.about {
    &-block {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding: 30px 0;
        &__thumb {
            margin-right: 135px;
            flex-shrink: 0;
            @include response(992px) {
                margin-right: 50px;
            }
            @include response(768px) {
               margin: 0;
               order: 10;
               width: 100%;
               text-align: center;
            }
        }
        &__title {
            font-size: 24px;
            font-weight: 700;
            color: #36434C;
            margin-bottom: 30px;
        }
        &__caption {
            &:first-letter {
                font-size: 36px;
                font-weight: 700;
                color: #0085E8;
            }
        }
        @include response(768px) {
            flex-wrap: wrap;
            justify-content: space-around;
        }
    }
    &-video {
        padding: 35px 0;
        max-width: 1019px;
        margin: 0 auto;
    }
}