.privacy {
    &-block {
        padding-bottom: 15px;
        border-bottom: 1px dotted #D7DFEB;
        max-width: 865px;
        margin: 0 auto 40px;
        &:last-child {
            padding: 0;
            border: none;
            margin-bottom: 80px;
        }
    }
    &-title{
        font-size: 22px;
        line-height: 26px;
        font-weight: 800;
        color: #36434C;
        margin-bottom: 15px;
    }
}