.header {
    margin-bottom: 20px;
    &-top {
        padding: 20px 0 24px;
        .page-inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        @include response(992px) {
            padding: 15px 0;
        }
    }
    &-left,
    &-right {
        display: flex;
        align-items: center;
    }
    &-right {
        @include response(992px) {
            display: none;
        }
    }
    &-account {
        a {
            font-size: 12px;
            font-weight: 800;
            color: #515964;
            text-decoration: none;
            @include transition();
            @include desktop-hover {
                color: $blue;
            }
        }
        .registration {
            margin-left: 2px;
            &::before {
                content: '/';
                margin-right: 3px;
                display: inline-block;
            }
            @include desktop-hover {
                &::before {
                    color: #515964;
                }
            }
        }
    }
    .logo {
        margin-right: 55px;
    }
    &-bot {
        background: #FAFBFC;
        border-top: 2px solid #F5F7FA;
        border-bottom: 2px solid #F5F7FA;
        .page-inner {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
    &-contact {
        display: flex;
        align-items: center;
        text-decoration: none;
        padding: 19px 35px 18px;
        border-left: 2px solid #F5F7FA;
        &__thumb {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            background: $blue;
            margin-right: 11px;
            svg {
                width: 11px;
                height: 11px;
            }
        }
        &__title {
            font-size: 12px;
            color: #9aa2ad;
        }
        &__content {
            font-size: 14px;
            text-decoration: none;
            color: $grey;
            font-weight: 800;
            transition: .3s ease-in-out;
        }
        @include desktop-hover {
            .header-contact__content {
                color: $blue;
            }
        }
        @include response(1120px) {
            padding: 19px 15px 18px;
        }
        @include response(992px) {
            display: none;
        }
    }
}
.navigation {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;
    margin-right: 25px;
    list-style: none;
    &__item {
        & + .navigation__item {
            margin-left: 25px;
        }
    }
    &__link {
        font-size: 12px;
        font-weight: 800;
        color: #515964;
        text-decoration: none;
        @include transition();
        @include desktop-hover {
            color: #7F8EA3;
        }
    }
    @include response(992px) {
        display: none;
    }
}
.track-link {
    font-size: 12px;
    font-weight: 800;
    color: $blue;
    cursor: pointer;
    @include transition();
    @include desktop-hover {
        color: $yellow;
    }
    @include response(992px) {
        display: none;
    }
}
.lang-switch {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;
    margin-right: 30px;
    list-style: none;
    &__item {
        padding: 0 5px;
        line-height: 13px;
        & + .lang-switch__item {
            border-left: 2px solid #96A4B7;
        }
    }
    a {
        color: #96A4B7;
        padding: 0 5px;
        text-transform: uppercase;
        text-decoration: none;
        font-weight: 800;
        font-size: 12px;
        @include transition();
        @include desktop-hover {
            color: $dark-blue;
        }
        &.active {
            color: $blue;
        }
    }
}
.catalog {
    margin-right: 65px;
    position: relative;
    &-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-weight: 700;
        background: $dark-blue;
        color: $white;
        height: 66px;
        width: 269px;
        transition: .3s ease-in-out;
        cursor: pointer;
        span {
            margin-top: 3px;
            letter-spacing: 2px;
        }
        svg {
            width: 16px;
            height: 16px;
            margin-right: 16px;
        }
        @include desktop-hover {
            background: rgba($dark-blue, .9);
        }
        @include response(767px) {
            max-width: 240px;
        }
    }
    &-list {
        position: absolute;
        width: 100%;
        left: 0;
        top: 100%;
        padding: 0;
        margin: 0;
        list-style: none;
        border: 1px solid #F5F7FA;
        border-top: none;
        border-radius: 2px;
        max-height: 385px;
        z-index: 5;
        display: none;
        a {
            text-decoration: none;
            display: flex;
            justify-content: space-between;
            padding: 0 26px 0 23px;
            line-height: 47px;
            font-family: 'Impact';
            color: #36434C;
            font-size: 15px;
            border-bottom: 1px solid #F5F7FA;
            transition: .3s ease-in-out;
            background: $white;
            @include desktop-hover {
                background: #F2F7FB;
            }
        }
        &__count {
            font-family: 'Gothic A1', sans-serif;
            font-size: 10px;
            color: #9AA2AD;
        }
        .mCSB_container {
            margin-right: 0px;
        }
        .mCSB_scrollTools {
            width: 3px;
            right: 0;
            .mCSB_dragger_bar {
                background: $vivid-blue !important;
            }
        }
        .mCSB_draggerRail {
            background: #EAEDF1;
            width: 3px;
        }
    }
    @include response(1120px) {
        margin-right: 15px;
    }
}
.feedback-btn {
    display: flex;
    color: $blue;
    font-size: 14px;
    background: #e3f6fe;
    margin-right: 63px;
    border-radius: 2px;
    padding: 14px 17px;
    font-weight: 700;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
        width: 11px;
        height: 11px;
        margin-right: 9px;
    }
    @include desktop-hover {
        background: $blue;
        color: $white;
    }
    @include response(992px) {
        display: none;
    }
}
.cart-btn {
    display: flex;
    align-items: center;
    padding: 0 25px;
    height: 66px;
    position: relative;
    border-left: 2px solid #F5F7FA;
    border-right: 2px solid #F5F7FA;
    transition: .3s ease-in-out;
    cursor: pointer;
    svg {
        width: 23px;
        height: 20px;
    }
    &__marker {
        position: absolute;
        width: 21px;
        height: 21px;
        background: $yellow;
        color: $white;
        font-weight: 700;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        font-size: 10px;
        padding-top: 1px;
        border: 2px solid #F5F8FC;
        top: 11px;
        right: 13px;
    }
    @include desktop-hover {
        background: $soft-grey;
    }
}
.burger {
    width: 25px;
    height: 14px;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    span,
    &::before,
    &::after {
        content: '';
        width: 100%;
        height: 2px;
        background: $dark-blue;
    }
    @include response(992px) {
        display: flex;
    }
}
