.breadcrumbs {
    margin: 0 0 40px;
    padding: 0 15px;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    li {
        padding: 0 15px;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            width: 5px;
            border-top: 1px solid #9EA7B4;
            left: 0;
            top: 5px;
        }
        &:first-child {
            &::before {
                display: none;
            }
        }
        &:last-child {
            &::before {
                border-color: #CFD5DC; 
            }
        }
    }
    a {
        color: #36434C;
        text-decoration: none;
        font-weight: 800;
        transition: .3s ease-in-out;
        @include desktop-hover {
            color: $blue;
        }
    }
    span {
        color: #CFD5DC;
        font-weight: 800;
    }
    @include response(767px) {
        font-size: 10px;
    }
}