.flow {
    width: 829px;
    height: 505px;
    background-image: url('../images/flow-bg.svg');
    margin: 0 auto 60px;
    position: relative;
    counter-reset: items;
    counter-reset: arrows;
    &-item  {
        position: absolute;
        display: flex;
        counter-increment: items;
        &__thumb {
            width: 86px;
            height: 86px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            background: $white;
            box-shadow: 0 0 20px 0 rgba($dark-blue, .1);
        }
        &__title {
            font-family: 'Roboto', sans-serif;
            font-size: 16px;
            font-weight: 700;
            color: #3E4144;
            position: relative;
            &::before {
                content: '0'counter(items);
                font-family: 'Roboto', sans-serif;
                font-size: 14px;
                font-weight: 500;
                color: #8AA0AD;
                position: absolute;
            }
        }
    }
    .item-1 {
        align-items: center;
        left: -150px;
        bottom: 99px;
        .flow-item__title {
            margin-top: 5px;
            margin-right: 25px;
            &::before {
                top: -25px;
                left: 0;
            }
        }
    }
    .item-2 {
        align-items: center;
        top: 111px;
        left: -57px;
        .flow-item__title {
            margin-top: 5px;
            margin-right: 35px;
            &::before {
                top: -25px;
                left: 0;
            }
        }
    }
    .item-3 {
        flex-direction: column;
        align-items: center;
        top: -38px;
        left: 355px;
        .flow-item__title {
           margin-bottom: 20px;
           &::before {
                top: -25px;
                left: 0;
            }
        }
    }
    .item-4 {
        flex-direction: row-reverse;
        align-items: center;
        top: 106px;
        right: -85px;
        .flow-item__title {
           margin-left: 25px;
           &::before {
                top: -25px;
                left: 0;
            }
        }
    }
    .item-5 {
        flex-direction: row-reverse;
        align-items: center;
        bottom: 98px;
        right: -156px;
        .flow-item__title {
           margin-left: 25px;
           &::before {
                top: -25px;
                left: 0;
            }
        }
    }
    .arrow {
        position: absolute;
        counter-increment: arrows;
        &::before {
            content: '0'counter(arrows);
            font-family: 'Roboto', sans-serif;
            font-size: 14px;
            font-weight: 500;
            color: #8AA0AD;
            position: absolute;
        }
        svg {
            width: 15px;
            height: 17px;
        }
    }
    .arrow-1 {
        left: 67px;
        bottom: 238px;
        &::before {
            top: -15px;
            left: -30px;
        }
    }
    .arrow-2 {
        left: 265px;
        top: 55px;
        svg {
            transform: rotate(37deg);
        }
        &::before {
            top: -30px;
            left: -5px;
        }
    }
    .arrow-3 {
        right: 272px;
        top: 54px;
        svg {
            transform: rotate(80deg);
        }
        &::before {
            top: -30px;
            left: 5px;
        }
    }
    .arrow-4 {
        right: 76px;
        bottom: 251px;
        svg {
            transform: rotate(118deg);
        }
        &::before {
            top: 2px;
            left: 29px;
        }
    }
    @include response(1200px) {
        width: 100%;
        height: auto;
        background: none;
        .arrow {
            display: none;
        }
        .flow-item {
            position: relative;
            top: auto;
            left: auto;
            right: auto;
            bottom: auto;
            justify-content: space-between;
            flex-direction: row;
            max-width: 320px;
            width: 100%;
            margin: 0 auto 50px;
            &__title {
                margin: 0 25px 0 0 !important;
            }
            &::before {
                content: '';
                width: 25px;
                height: 27px;
                background-image: url('../images/arrow-b.svg');
                background-position: center;
                background-size: cover;
                position: absolute;
                right: 27px;
                transform: rotate(147deg);
                bottom: -38px;
            }
            &:last-child::before {
                display: none;
            }
        }
    }
}