.faq-page {
    .accordion {
        max-width: 860px;
        margin: 0 auto 80px;
        &-item {
            margin-bottom: 10px;
            border: 1px solid #E5EDF8;
            &.active {
                .opener {
                    background: #F5FBFF;
                    &::before {
                        height: 100%;
                    }
                    &::after {
                        transform: translateY(-50%) rotate(180deg);
                        background-color: rgba(#88D8FA, .18);
                        background-image: url(../images/arrow-g.svg);
                    }
                }
            }
        }
        .opener {
            padding: 25px 45px;
            padding-right: 60px;
            font-size: 14px;
            font-weight: 800;
            color: #272727;
            position: relative;
            transition: .3s ease-in-out;
            cursor: pointer;
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: -1px;
                height: 0;
                border-right: 3px solid #0085E8;
                transition: .3s ease-in-out;
            }
            &::after {
                content: '';
                position: absolute;
                width: 22px;
                height: 22px;
                border: none;
                border-radius: 50%;
                background-color: #88D8FA;
                background-image: url(../images/arrow-w.svg);
                background-repeat: no-repeat;
                background-position: center;
                top: 50%;
                right: 24px;
                transform: translateY(-50%);
                transform-origin: 50% 50%;
                transition: .3s ease-in-out;
            }
            @include desktop-hover {
                background: #F5FBFF;
            }
            @include response(767px) {
                padding: 20px 15px;
                padding-right: 55px;
            }
        }
        .slide {
            padding: 25px 45px 5px;
            font-size: 14px;
            line-height: 21px;
            color: #7A8087;
            @include response(767px) {
                padding: 20px 15px 5px;
            }
        }
    }
}
