.services {
    &-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 55px;
        &__thumb {
            flex-shrink: 0;
            margin-right: 49px;
            @include response(768px) {
                max-width: 320px;
            }
            @include response(767px) {
                margin: 0 0 30px;
            }
        }
        &__title {
            font-size: 32px;
            font-weight: 900;
            line-height: 36px;
            margin-bottom: 30px;
            color: #36434C;
        }
        &__content {
            margin-bottom: 50px;
            &-inner {
                height: 125px;
                overflow: hidden;
                position: relative;
                margin-bottom: 5px;
                transition: .5s ease-in;
                &::after {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 60px;
                    background: linear-gradient(to top, rgba(255,255,255,1) 0%,rgba(255,255,255,0.05) 100%);
                    transition: .3s ease-in-out;
                }
                &.active::after {
                    opacity: 0;
                }
            }
        }
        &__caption {
            font-size: 13px;
            line-height: 20px;
            color: #7A8087;
            p {
                margin: 0 0 20px;
            }
        }
        &__more {
            color: $blue;
            display: inline-block;
            border-bottom: 1px dotted $blue;
            font-weight: 700;
            padding-bottom: 2px;
            cursor: pointer;
        }
        &__info {
            display: inline-flex;
            .services-price {
                background: rgba(#EAF8FE, .18);
                border-radius: 6px;
                padding: 9px 16px;
                margin-right: 22px;
            }
        }
        @include response(767px) {
            flex-wrap: wrap;
            justify-content: space-around;
        }
    }
    &-price {
        font-size: 13px;
        color: #36434C;
        &__label {
            font-size: 12px;
            font-weight: 500;
            color: #9AA2AD;
            margin-bottom: 3px;
        }
        &__ammount {
            font-size: 22px;
            font-weight: 800;
        }
    }
    &-holder {
        border-top: 1px solid #E5EDF8;
        padding-top: 50px;
        .page-inner {
            display: flex;
            justify-content: space-between;
            align-self: flex-start;
        }
    }
    &-inner {
        .row {
            margin-bottom: 15px;
            @include response(768px) {
                justify-content: space-between;
            }
            @include response(767px) {
                flex-wrap: wrap;
                justify-content: flex-start;
            }
        }
    }
    &-sort {
        display: flex;
        align-items: center;
        margin-right: 35px;
        &__label {
            font-weight: 500;
            font-size: 12px;
            color: #9AA2AD;
        }
        &:last-child {
            margin-right: 0;
        }
        @include response(767px) {
            width: 100%;
        }
    }
    &-item {
        padding: 30px;
        border: 1px solid #E5EDF8;
        border-radius: 4px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 20px;
        &__thumb {
            margin-right: 40px;
            flex-shrink: 0;
            @include response(767px) {
                margin: 0 0 20px;
            }
        }
        &__slider {
            border-radius: 2px;
            overflow: hidden;
            margin-bottom: 15px;
            width: 345px;
            @include response(767px) {
                width: 320px;
            }
        }
        &__nav {
            width: 345px;
            padding: 0 16px;
            img {
                margin: 0 auto;
                border-radius: 3px;
            }
            &-item {
                
                overflow: hidden;
            }
            .slick-slide {
                opacity: .5;
                position: relative;
                padding-bottom: 3px;
                transition: .3s ease-in-out;
                &::after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 0;
                    border-top: 2px solid #0085E8;
                    transition: .3s ease-in-out;
                }
                &.slick-current {
                    opacity: 1;
                    &::after {
                        width: 100%;
                    }
                }
            }
            .slick-arrow {
                top: 0;
                transform: none;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 40px;
                background: #EAF8FE;
                width: 12px;
                &::before {
                    content: '';
                    display: block;
                    width: 8px;
                    height: 4px;
                    background-image: url(../images/arrow-d.svg);
                    background-repeat: no-repeat;
                    background-position: center;
                }
            }
            .slick-prev {
                left: 0;
                &::before {
                    transform: rotate(90deg);
                }
            }
            .slick-next {
                right: 0;
                &::before {
                    transform: rotate(-90deg);
                }
            }
            @include response(767px) {
                width: 320px;
            }
        }
        &__inner {
            max-width: 100%;
        }
        &__title {
            font-family: 'Roboto', sans-serif;
            font-size: 18px;
            font-weight: 700;
            color: #272727;
            margin-bottom: 20px;
        }
        &__info {
            margin-bottom: 35px;
        }
        &__line {
            margin-bottom: 15px;
        }
        &__label {
            font-size: 10px;
            font-weight: 500;
            margin-bottom: 3px;
            letter-spacing: .5px;
            color: #9AA2AD;
        }
        &__content {
            font-size: 13px;
            font-weight: 700;
            line-height: 20px;
            color: #515A66;
        }
        &__caption {
            font-size: 13px;
            line-height: 18px;
            color: #7A8087;
        }
        .services-price {
            margin-bottom: 15px;
        }
        @include response(767px) {
            width: 100%;
            flex-wrap: wrap;
            justify-content: space-around;
            padding: 15px 0;
            border: none;
            border-bottom: 1px solid #E5EDF8;
            border-radius: 0;

        }
    }
    &-more {
        display: flex;
        width: 100%;
        height: 67px;
        justify-content: center;
        align-items: center;
        background: #F9FDFF;
        border: 1px solid #E5EDF8;
        text-decoration: none;
        color: #272727;
        font-weight: 700;
        font-size: 14px;
        transition: .3s ease-in-out;
        span {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 28px;
            height: 28px;
            border-radius: 50%;
            margin-right: 12px;
            margin-bottom: 2px;
            background: rgba(#88D8FA, .18);
            transition: .3s ease-in-out;
        }
        svg {
            width: 6px;
            height: 10px;
            transform: rotate(-90deg);
            
            fill: #6E889B;
        }
        @include desktop-hover {
            background: $soft-blue;
            color: $white;
            span {
                background: rgba($white, .5);
            }
        }
    }
}
.buy-btn {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
}
.offers-link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: rgba(#88D8FA, .18);
    border-radius: 6px;
    padding: 0 21px;
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    color: #272727;
    transition: .3s ease-in-out;
    span {
        flex-shrink: 0;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #88D8FA;
        margin-right: 9px;
    }
    svg {
        height: 7px;
        width: 4px;
        fill: #fff;
        transform: rotate(-90deg);
    }
    @include desktop-hover {
        background: $soft-blue;
        color: $white;
    }
}