.error {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 200px 0 160px;
    background-image: url('../images/error-bg.svg');
    background-position: center;
    background-repeat: no-repeat;
    &-inner {
        text-align: center;
       
        .btn {
            font-size: 14px;
            font-weight: 700;
            border-radius: 2px;
        }
    }
    &-thumb {
        margin-bottom: 45px;
    }
    &-title {
        font-size: 28px;
        font-weight: 900;
        color: #3E4144;
        margin-bottom: 20px;
    }
    &-caption {
        font-size: 14px;
        line-height: 21px;
        color: #7A8087;
        max-width: 455px;
        margin: 0 auto 25px;
    }
    @include response(1100px) {
        padding: 80px 0;
        background-position: 0 0;
    }
}