.delivery {
    &-block {
        max-width: 865px;
        margin: 0 auto 60px;
        &:last-child {
            margin-bottom: 80px;
        }
    }
    &-triggers {
        display: flex;
        &__item {
            width: 33%;
            border: 1px solid #E5EDF8;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 263px;
            padding: 30px 15px;
            @include response(767px) {
                height: auto;
            }
            img {
                max-width: 70%;
            }
        }
    }
}