select{
  display: none;
}
.select {
  padding: 10px 15px;
  padding-right: 40px;
  position: relative;
  user-select: none;
  cursor: pointer;
  margin: 0;
  white-space: nowrap;
  outline: none;
  overflow: hidden;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 10px;
    width: 22px;
    height: 22px;
    border: none;
    border-radius: 50%;
    background-color: rgba(136, 216, 250, 0.18);
    background-image: url(../images/arrow-d.svg);
    background-repeat: no-repeat;
    background-position: center;
    transform: translateY(-50%);
    margin: 0;
    transform-origin: 50% 50%;
    z-index: 2;
    transition: .3s ease-in-out;
  }
  &:hover {
    border-color: #dbdbdb;
  }
  &:focus{
    border-color: #666;
  }
  &--open {
    overflow: visible;
    &::after {
      transform: translateY(-50%) rotate(180deg);
    }
  }
  &--medium {
    max-width: 30em;
  }
  &__current {
    font-weight: 700;
    color: #515A66;
    font-size: 12px;
  }
  &__list {
    position: absolute;
    @extend %clear-list;
    width: 100%;
    left: 0;
    top: 100%;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 1px 1px rgba($soft-blue, .7);
    box-sizing: border-box;
    margin-top: .2em;
    opacity: 0;
    overflow: hidden;
    padding: 0;
    transform-origin: 50% 0;
    transform: scale(0.75) translateY(-2em);
    transition: transform 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.1s ease-out;
    z-index: -1;
    .select--open & {
      visibility: visible;
      opacity: 1;
      z-index: 3;
      transform: scale(1) translateY(0);
    }
  }
  &__item {
    padding: .5em 1em;
    white-space: normal;
    cursor: pointer;
    @include desktop-hover {
      background-color: rgba(#88D8FA, .18);
    }
    &--active {
        background-color: rgba(#88D8FA, .18);
        font-weight: 700;
    }
    &--disabled {
      opacity: .3;
      cursor: not-allowed;
    }
  }
}