@mixin transition($duration: .3s) {
    transition: all $duration ease-in-out;
}
.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .page {
        flex: 1 1 auto;
        position: relative;
    }
    .footer {
        flex: 0 0 auto;
    }
}
.page-title {
    font-size: 32px;
    line-height: 25px;
    color: #36434C;
    font-weight: 900;
    text-align: center;
    margin-bottom: 25px;
    span {
        display: block;
    }
    svg{
        width: 77px;
        height: 6px;
    }
    @include response(767px) {
        font-size: 24px;
    }
}
.page-inner {
    max-width: 1170px;
    padding: 0 15px;
    margin: 0 auto;
}
.btn {
    height: 48px;
    line-height: 48px;
    display: inline-block;
    font-family: 'Gothic A1', sans-serif;
    font-size: 12px;
    font-weight: 900;
    color: $blue;
    text-align: center;
    background: rgba(#88D8FA, .18);
    width: 190px;
    text-decoration: none;
    border-radius: 5px;
    transition: .3s ease-in-out;
    border: none;
    padding: 0;
    cursor: pointer;
    outline: none;
    @include desktop-hover {
        color: $white;
        background: $blue;
    }
    &--line {
        background: transparent;
        border: 1px solid $blue;
        border-radius: 5px;
    }
    &--blue {
        background: $blue;
        color: $white;
        border: 1px solid $blue;
        line-height: 46px;
        @include desktop-hover {
            background: transparent;
            color: $blue;
        }
    }
}
.btn-line {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &::before,
    &::after {
        content: '';
        width: 100%;
        border-top: 1px solid #E9EEF1;
    }
    .btn {
        flex-shrink: 0;
        margin: 0 29px;
    }
}
.text-caption {
    padding: 125px 15px 70px;
    background-image: url(../images/seo-bg.svg);
    &__inner {
        max-width: 1080px;
        margin: 0 auto;
        position: relative;
        .decor-item-3 {
            left: -20% !important;
            top: 50% !important;
        }
        .decor-item-1 {
            right: 5% !important;
            top: -5% !important;
            left: auto !important;
        }
        .decor-item-2 {
            right: -5% !important;
            top: 5% !important;
            left: auto !important;
        }
    }
    &__content {
        font-size: 14px;
        line-height: 21px;
        color: #7A8087;
        max-height: 160px;
        p {
            margin: 0 0 20px;
        }
    }
    .mCSB_scrollTools .mCSB_draggerRail {
        width: 3px;
        background: #E3E8F0;
        border-radius: 0;
    }
    .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
        width: 3px;
        background: $blue;
        border-radius: 0;
    }
}
.row {
    display: flex;
    align-items: center;
    &--end {
        justify-content: flex-end;
    }
    &--between {
        justify-content: space-between;
    }
}
.content-area {
    font-size: 14px;
    line-height: 21px;
    color: #515A66;
    p {
        margin: 0 0 25px;
    }
}
iframe {
    border: none;
    max-width: 100%;
}
.form {
    &-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .form-column {
            width: 48%;
            @include response(767px) {
                width: 100%;
            }
        }
        .form-group {
            width: 100%;
            min-height: 63px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include response(767px) {
                min-height: 0px;
            }
        }
        @include response(767px) {
            flex-wrap: wrap;
        }
    }
    &-group {
        position: relative;
        margin-bottom: 25px;
    }
    &-label {
        font-size: 12px;
        font-weight: 700;
        color: #B5C0CF;
        margin-bottom: 5px;
        display: block;
    }
    &-field {
        width: 100%;
        height: 45px;
        outline: none;
        line-height: 43px;
        border: 1px solid #DEECF4;
        border-radius: 5px;
        background: #F7FCFF;
        padding: 0 21px;
        font-family: 'Gothic A1', sans-serif;
        font-size: 14px;
        font-weight: 700;
        color: #515A66;
        &::placeholder {
            font-family: 'Roboto', sans-serif;
            font-size: 12px;
            font-weight: 400;
            color: rgba(139, 155, 176, 0.62);
        }
    }
    &-area {
        width: 100%;
        height: 127px;
        outline: none;
        line-height: 21px;
        border: 1px solid #DEECF4;
        border-radius: 5px;
        background: #F7FCFF;
        padding: 15px 21px;
        font-family: 'Gothic A1', sans-serif;
        font-size: 14px;
        font-weight: 700;
        color: #515A66;
    }
    &-info {
        position: relative;
        margin-left: 10px;
        margin-top: 1px;
        cursor: help;
        svg {
            width: 10px;
            height: 10px;
            
        }
        &__caption {
            position: absolute;
            background: #fff;
            padding: 8px;
            width: 190px;
            font-size: 10px;
            font-weight: 600;
            color: #8D99AA;
            box-shadow: 0 3px 8px 0 rgba(219, 227, 236, 0.63);
            border-radius: 5px;
            top: -40px;
            text-align: center;
            left: -130%;
            z-index: 2;
            display: none;
        }
        @include desktop-hover {
            .form-info__caption {
                display: block;
            }
        }
    }
}
.custom-checkbox {
    display: flex;
    align-items: center;
    cursor: pointer;
    input {
        width: 0;
        height: 0;
        opacity: 0;
        z-index: -1;
        &:checked ~ .custom-checkbox__box:before {
            transform: scale(1);
        }
    }
    &__box {
        width: 15px;
        height: 15px;
        background: #F7FCFF;
        border: 1px solid #DEECF4;
        border-radius: 50%;
        margin-right: 12px;
        transition: .3s ease-in-out;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            content: '';
            width: 9px;
            height: 9px;
            border-radius: 50%;
            background: $blue;
            transition: .3s ease-in-out;
            transform: scale(0)
        }
    }
    &__label {
        font-size: 12px;
        font-weight: 700;
        color: #6E7682;
    }
}
.center {
    text-align: center;
}
.callback-btn {
    position: fixed;
    right: 150px;
    bottom: 280px;
    width: 61px;
    height: 61px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    background: linear-gradient(to bottom, rgba(28,200,245,1) 0%,rgba(19,169,244,1) 100%);
    svg {
        width: 22px;
        height: 22px;
        transition: .3s ease-in-out;
    }
    &::before {
        content: '';
        width: 69px;
        height: 69px;
        border: 1px solid #15AFF5;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        transition: .4s ease-in-out;
    }
    @include desktop-hover {
        &::before {
            transform: translate(-50%, -50%) scale(0.9);
        }
        svg {
            transform: scale(1.2);
        }
    }
    @include response(1280px) {
        right: 25px;
    }
}
.decor-item {
    width: 22px;
    height: 22px;
    position: absolute;
    @include response(1280px) {
        display: none;
    }
}
.quantity {
    display: flex;
    &__btn {
        height: 28px;
        width: 28px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #EFF3F8;
        cursor: pointer;
        transition: .3s ease-in-out;
        @include desktop-hover {
            background: #E8F7FF;
        }
        svg {
            width: 7px;
            height: 7px;
        }
        &.inc {
            border-radius: 0 5px 5px 0;
        }
        &.dec {
            border-radius: 5px 0 0 5px;
        }
    }
    &__field {
        width: 34px;
        border: none;
        border-top: 1px solid #DEECF4;
        border-bottom: 1px solid #DEECF4;
        font-family: 'Gothic A1', sans-serif;
        font-size: 12px;
        font-weight: 700;
        color: #36434C;
        text-align: center;
        background: #fff;
        outline: none;
    }
}