.products {
    &-block {
        margin-bottom: 65px;
    }
    &-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        @include response(1170px) {
            .product {
                width: 32%;
            }
        }
        @include response(767px) {
            .product {
                width: auto;
            }
        }
        .slick-dots {
            display: flex;
            align-items: center;
            justify-content: center;
            bottom: 0;
            li {
                width: 11px;
                height: 11px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                border: 1px solid transparent;
                &.slick-active {
                    border-color: $grey;
                }
            }
            button {
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background: $grey;
                padding: 0;
                &::before {
                    display: none;
                }
            }
        }
    }
}
.product {
    text-decoration: none;
    margin-bottom: 30px;
    &__thumb {
        margin-bottom: 13px;
        border-radius: 2px;
        overflow: hidden;
        background: $dark-blue;
        img {
            transition: .4s ease-in-out;
        }
    }
    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__title {
        font-size: 14px;
        font-weight: 900;
        color: #272727;
        transition: .3s ease-in-out;
    }
    &__price {
        font-family: 'Roboto', sans-serif;
        height: 34px;
        line-height: 34px;
        background: $blue;
        color: $white;
        padding: 0 15px;
        border-radius: 2px;
        font-size: 10px;
        span {
            font-size: 14px;
            font-weight: 700;
        }
    }
    @include desktop-hover {
        .product__title {
            color: $blue;
        }
        .product__thumb img {
            transform: scale(1.1);
            opacity: .5;
        }
    }
}