.service {
    &-page {
        .decor-item-2 {
            top: 20%;
            left: 10%;
        }
        .decor-item-3 {
            top: 36%;
            left: 6%;
        }
        .decor-item-4 {
            top: 8%;
            right: 6%;
        }
        .decor-item-5 {
            top: 25%;
            right: 7%;
        }
    }
    &-slider {
        margin-bottom: 15px;
        overflow: hidden;
        img {
            border-radius: 4px;
        }
        .slick-arrow {
            width: 92px;
            height: 92px;
            background: #fff;
            z-index: 3;
            border: none;
            border-radius: 50%;
            box-shadow: 0 0 0 15px rgba($white, .2);
            display: flex;
            align-items: center;
            justify-content: center;
            &::before {
                content: '';
                width: 12px;
                height: 8px;
                display: block;
                background-image: url(../images/arrow-d.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
        .slick-prev {
            left: -45px;
            padding-left: 25px;
            &::before {
                transform: rotate(90deg);
            }
        }
        .slick-next {
            right: -45px;
            padding-right: 25px;
            &::before {
                transform: rotate(-90deg);
            }
        }
    }
    &-nav {
        padding: 0 25px;
        margin-bottom: 30px;
        img {
            margin: 0 auto;
            border-radius: 3px;
        }
        &-item {
                
            overflow: hidden;
        }
        .slick-slide {
            opacity: .5;
            position: relative;
            padding-bottom: 3px;
            transition: .3s ease-in-out;
            &::after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 0;
                border-top: 2px solid #0085E8;
                transition: .3s ease-in-out;
            }
            &.slick-current {
                opacity: 1;
                &::after {
                    width: 121px;
                }
            }
        }
        .slick-arrow {
            top: 0;
            transform: none;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 80px;
            background: #EAF8FE;
            width: 25px;
            &::before {
                content: '';
                display: block;
                width: 8px;
                height: 4px;
                background-image: url(../images/arrow-d.svg);
                background-repeat: no-repeat;
                background-position: center;
            }
        }
        .slick-prev {
            left: 0;
            &::before {
                transform: rotate(90deg);
            }
        }
        .slick-next {
            right: 0;
            &::before {
                transform: rotate(-90deg);
            }
        }
    }
    &-holder {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-bottom: 29px;
        @include response(992px) {
            flex-wrap: wrap;
            justify-content: space-around;
        }
    }
    &-content {
        padding: 35px;
        background: $white;
        width: 100%;
        &__title {
            font-size: 22px;
            font-weight: 800;
            color: #272727;
            line-height: 25px;
            margin-bottom: 10px;
        }
        .content-title {
            font-size: 18px;
            font-weight: 800;
            color: #272727;
            line-height: 23px;
            margin-bottom: 15px;
        }
        @include response(992px) {
            margin-bottom: 30px;
        }
        @include response(767px) {
            padding: 25px 15px;
        }
    }
    &-info {
        border-bottom: 1px solid #E5EDF8;
        margin: 0 -35px 40px;
        padding: 0 35px 20px;
        &__line {
            display: flex;
            align-items: flex-end;
            margin-bottom: 20px;
            .separator {
                width: 100%;
                border-bottom: 1px dotted #C8CFD9;
                @include response(767px) {
                    margin: 5px 0;
                }
            }
            @include response(767px) {
                flex-wrap: wrap;
            }
        }
        &__title {
            font-size: 14px;
            color: #9AA2AD;
            display: flex;
            align-items: center;
            flex-shrink: 0;
            svg {
                width: 12px;
                height: 16px;
                margin-right: 8px;
                fill: #9AA2AD;
            }
        }
        &__caption {
            flex-shrink: 0;
            @include response(767px) {
                flex-shrink: 1;
            }
        }
        @include response(767px) {
            margin: 0 -15px 30px;
            padding: 0 15px;
        }
    }
    &-sidebar {
        width: 287px;
        margin-left: 6px;
        flex-shrink: 0;
        background: $white;
        padding: 35px 25px 17px 25px;
        text-align: center;
        &__thumb {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 152px;
            height: 152px;
            border-radius: 50%;
            overflow: hidden;
            border: 1px solid #E5EDF8;
            margin: 0 auto 25px;
        }
        &__title {
            font-size: 16px;
            font-weight: 800;
            color: #808791;
            letter-spacing: 1px;
            margin-bottom: 19px;
        }
        &__label {
            font-size: 12px;
            font-weight: 500;
            margin-bottom: 10px;
            color: #9AA2AD;
        }
        &__date {
            padding: 8px 16px;
            background: #F3FAFF;
            border-radius: 2px;
            font-size: 14px;
            font-weight: 700;
            color: #515A66;
            display: inline-block;
        }
        .services-price {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 25px;
            &__label {
                margin-right: 15px
            }
        }
        .btn {
            width: 100%;
            height: 46px;
            line-height: 44px;
            margin-bottom: 13px;
        }
    }
}
.bg-section {
    padding: 30px 0;
    background: #F5F7FA;
}