.footer {
    background: #FAFBFC;
    &__top {
        border-bottom: 1px solid #E7ECF4;
        padding: 15px 0;
    }
    &__inner {
        max-width: 1170px;
        margin: 0 auto;
        padding: 0 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include response(767px) {
            flex-wrap: wrap;
            justify-content: center;
        }
    }
    &-holder {
        padding: 35px 0 55px;
        .footer__inner {
            align-items: flex-start;
        }
        @include response(767px) {
            padding: 20px 0;
        }
    }
    &-item {
        min-width: 150px;
        position: relative;
        &-title {
            font-size: 14px;
            font-weight: 800;
            text-transform: uppercase;
            min-width: 150px;
            position: absolute;
            left: 0;
            top: -83px;
            @include response(767px) {
                display: none;
            }
        }
        @include response(1100px) {
            &.nav {
                display: none;
            }
        }
        @include response(767px) {
            width: 100%;
        }
    }
    &-nav {
        padding: 0;
        margin: 0;
        list-style: none;
        columns: 2;
        width: 340px;
        margin-bottom: 6px;
        &__title {
            width: 350px;
        }
        li {
            margin-bottom: 5px;
        }
        a {
            font-size: 14px;
            font-weight: 700;
            color: #ACBACC;
            transition: .3s ease-in-out;
            text-decoration: none;
            line-height: 30px;
            @include desktop-hover {
                color: $dark-blue;
            }
        }
    }
    &-bot {
        border-top: 1px solid #E7ECF4;
        padding: 15px 0;
    }
    .track-link {
        font-size: 14px;
        font-weight: 700;
    }
}
.sign-btn {
    height: 34px;
    line-height: 32px;
    font-weight: 800;
    max-width: 160px;
    border-radius: 3px;
    @include response(767px) {
        margin-bottom: 15px;
    }
}
.contacts-item {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
    color: #515A66;
    margin-bottom: 18px;
    transition: .3s ease-in-out;
    &__thumb {
        width: 23px;
        height: 23px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #9DD3EC;
        border-radius: 50%;
        margin-right: 15px;
        transition: .3s ease-in-out;
        svg {
            width: 10px;
            height: 10px;
            fill: $white;
        }
    }
    @include desktop-hover {
        color: $blue;
        .contacts-item__thumb {
            background: $soft-blue;
        }
    }
    @include response(767px) {
        justify-content: center;
    }
}
.socials {
    display: flex;
    align-items: center;
    &__item {
        width: 42px;
        height: 42px;
        margin: 0 6px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        border-radius: 10px;
        box-shadow: 0 0 15px 0 rgba($black, .1);
        transition: .4s ease-in-out;
        svg {
            width: 13px;
            height: 13px;
        }
        @include desktop-hover {
            box-shadow: 0 0 35px rgba($black, .2);
        }
    }
    @include response(767px) {
        justify-content: center;
    }
}
.payment-icons {
    display: flex;
    align-items: center;
    img {
        margin: 0 6px;
    }
    @include response(767px) {
        display: none;
    }
}
.copyright {
    color: #A9B0B9;
    font-size: 12px;
    @include response(767px) {
        text-align: center;
    }
}