.pagination {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    li {
        margin: 0 6px;
    }
    a {
        width: 76px;
        height: 67px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        color: #272727;
        font-size: 16px;
        font-weight: 700;
        border: 1px solid #E5EDF8;
        position: relative;
        transition: .3s ease-in-out;
        &::before {
            content: '';
            position: absolute;
            left: 0;
            bottom: -1px;
            width: 0;
            border-top: 3px solid $blue;
        }
        &.current {
            background: #F5FBFF;
            &::before {
                width: 100%;
            }
        }
        @include desktop-hover {
            background: #F5FBFF;
        }
        @include response(1200px) {
            height: 47px;
            width: 37px
        }
    }
    svg {
        width: 6px;
        height: 13px;
        fill: #6E889B;
        opacity: .5;
        transition: .3s ease-in-out;
    }
    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 28px;
        height: 28px;
        border-radius: 50%;
        background: rgba(#88D8FA, .18);
        flex-shrink: 0;
        margin: 0 11px;
    }
    .prev,
    .next {
        width: 350px;
        color: #BDC4CD;
        transition: .3s ease-in-out;
        @include desktop-hover {
            color: #272727;
            background: transparent;
            svg {
                opacity: 1;
            }
        }
        @include response(1200px) {
            width: auto;
            padding: 0 20px;
        }
        @include response(767px) {
            padding: 0 5px;
            span {
                display: none;
            }
        }
    }
    .next {
        svg {
            transform: scale(-1, -1);
        }
    }
}