.authorization {
    margin: 0 auto 70px;
    max-width: 706px;
    &-tabs {
        display: flex;
        align-items: center;
        list-style: none;
        padding: 0;
        margin: 0 0 35px;
        border: 1px solid #E5EDF8;
        li {
            width: 50%;
        }
        a {
            display: block;
            text-align: center;
            font-size: 16px;
            font-weight: 800;
            color: #AFB1B8;
            text-decoration: none;
            padding: 25px 0;
            border-bottom: 2px solid #E5EDF8;
            position: relative;
            transition: .3s ease-in-out;
            &::after {
                content: '';
                position: absolute;
                left: 0;
                bottom: -3px;
                width: 0;
                border-bottom: 3px solid #0085E8;
                transition: .3s ease-in-out;
            }
            &.active {
                color: #0085E8;
                &::after {
                    width: 100%;
                }
            }
            @include desktop-hover {
                color: #0085E8;
            }
        }
    }
    .btn {
        width: 100%;
        font-size: 14px;
        font-weight: 700;
    }
    &-social {
        max-width: 336px;
        margin: 0 auto;
        text-align: center;
        &__separator {
            font-size: 13px;
            font-weight: 500;
            color: #9AA2AD;
            display: flex;
            align-items: center;
            margin-bottom: 27px;
            span {
                flex-shrink: 0;
                margin: 0 10px;
            }
            &::before,
            &::after {
                content: '';
                width: 100%;
                border-top: 1px solid #E9EEF1;
            }
        }
        &__btn {
            background: rgba(#88D8FA, .18);
            display: block;
            padding: 8px 15px;
            text-decoration: none;
            border-radius: 5px;
            transition: .3s ease-in;
            @include desktop-hover {
                background: rgba(#88D8FA, .4);
            }
        }
    }
}