.filter {
    width: 308px;
    margin-right: 25px;
    flex-shrink: 0;
    position: relative;
    &-handler {
        height: 30px;
        width: 30px;
        fill: $dark-blue;
        display: none;
        @include response(767px) {
            display: block;
        }
    }
    &-close {
        position: absolute;
        width: 20px;
        height: 20px;
        top: 15px;
        left: 15px;
        display: none;
        @include response(767px) {
            display: block;
        }
    }
    &__title {
        font-size: 22px;
        line-height: 28px;
        font-weight: 800;
        color: #36434C;
        margin-bottom: 20px;
    }
    &-inner {
        padding: 25px 17px 17px;
        border-radius: 6px;
        background: rgba(#EAF8FE, .35);
    }
    &-item {
        padding-bottom: 15px;
        margin-bottom: 20px;
        border-bottom: 1px dashed #D6DFEB;
    }
    &__label {
        font-size: 12px;
        color: #B5C0CF;
        margin-bottom: 8px;
    }
    &__locations.select {
        margin: 0 0 11px;
        background: #fff;
        border-radius: 3px;
        padding: 17px 20px;
        border: 1px solid #E5EDF8;
        transition: .3s ease-in-out;
        .select__current {
            font-size: 14px;
            font-weight: 700;
            color: #272727;
        }
        &::after {
            width: 22px;
            height: 22px;
            border: none;
            border-radius: 50%;
            background-color: rgba(#88D8FA, .18);
            background-image: url('../images/arrow-d.svg');
            background-repeat: no-repeat;
            background-position: center;
            transform: translateY(-50%);
            margin: 0;
            transform-origin: 50% 50%;
        }
        &.select--open {
            border-color: $soft-blue;
        }
        @include desktop-hover {
            border-color: $soft-blue;
        }
    }
    &-btn {
        height: 34px;
        line-height: 32px;
        cursor: pointer;
        font-weight: 700;
        display: block;
        max-width: 170px;
        margin: 0 auto;
    }
    .noUi-horizontal {
        height: 4px;
        border-radius: 0;
        background: #C3E9FF;
        border: none;
        margin-top: 40px;
        .noUi-handle {
            right: -14px !important;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background: $white;
            border: 3px solid $blue;
            box-shadow: 0 0 5px 0 rgba($black, .1);
            outline: none;
            &::before,
            &::after {
                display: none;
            }
            .noUi-tooltip {
                bottom: 175%;
                padding: 8px 5px;
                font-size: 10px;
                font-weight: 800;
                color: #4D535A;
                border: none;
                box-shadow: 0 0 8px 0 rgba($black, .1);
            }
        }
    }
    .noUi-connects {
        border-radius: 0;
    }
    .noUi-connect {
        background: $blue;
    }
    @include response(768px) {
        position: fixed;
        left: 0;
        top: 0;
        transform: translateX(-100%);
        background: #fff;
        height: 100%;
        transition: .5s ease-in-out;
        z-index: 5;
        padding: 45px 10px 10px 10px;
        &.active {
            transform: translateX(0);
        }
    }
}
.active-locations {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &__item {
        display: flex;
        align-items: center;
        background: #EAF8FE;
        padding: 7px 12px;
        font-size: 12px;
        font-weight: 700;
        margin-right: 8px;
        margin-bottom: 8px;
        color: #515A66;
        svg {
            width: 6px;
            height: 6px;
        }
    }
    &__remove {
        margin-left: 10px;
        flex-shrink: 0;
        cursor: pointer;
    }
}