.pop-up {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 20;
    &__inner {
        width: 100%;
        position: relative;
        z-index: 2;
        background: #fff;
        border-radius: 6px;
        .contacts-form__title {
            font-size: 22px;
            line-height: 22px;
        }
    }
    &__caption {
        font-size: 12px;
        line-height: 18px;
        color: #7A8087;
        text-align: center;
        max-width: 336px;
        margin: 0 auto 25px;
    }
    &__close {
        position: absolute;
        top: -11px;
        right: -11px;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $blue;
        cursor: pointer;
        svg {
            width: 10px;
            height: 10px;
        }
        @include response(767px) {
            right: 10px;
        }
    }
    &__overlay {
        position: absolute;
        left: 0;
        top: 0;
        background: rgba(#000, .6);
        width: 100%;
        height: 100%;
    }
    &.active {
        display: flex;
    }
}
.callback-pop-up {
    max-width: 416px;
    padding: 35px 40px;
    .contacts-form__title {
        margin: 0 -40px 25px;
        padding: 0 40px 24px;
    }
}
.track-form {
    max-width: 381px;
    padding: 25px;
    .contacts-form__title {
        margin: 0 -25px 25px;
        padding: 0 25px 21px;
        flex-direction: column;
        align-items: center;
        img {
            margin-bottom: 10px;
        }
    }
}
.track-confirm {
    max-width: 381px;
    .form-field {
        background: #fff;
    }
    .contacts-form__title {
        flex-direction: column;
        align-items: center;
        padding: 35px 40px 0;
        text-align: center;
        margin-bottom: 0;
        img {
            margin-bottom: 10px;
        }
    }
    .pop-up__caption {
        margin: 10px 0 25px;
    }
}
.track-inner {
    padding: 25px 25px 35px;
    background: #F7FCFF;
    border-radius: 0 0 6px 6px;
    .page-link {
        margin-top: 15px;
        font-size: 12px;
        font-weight: 700;
        color: #B5C0CF;
        a {
            color: #8490A0;
            text-decoration: none;
            border-bottom: 1px dotted #8490A0;
            transition: .3s ease-in-out;
            @include desktop-hover {
                color: $blue;
            }
        }
    }
}
.success-pop-up {
    max-width: 381px;
    padding: 40px 20px 0;
    .contacts-form__title {
        flex-direction: column;
        align-items: center;
        padding: 0 20px 24px;
        margin: 0 -20px 25px;
        img {
            margin-bottom: 15px;
        }
    }
    .btn {
        width: 150px;
        margin-bottom: 35px;
        height: 38px;
        line-height: 36px;
    }
}
.cart-pop-up {
    max-width: 755px;
    .contacts-form__title {
        justify-content: flex-start;
        padding: 35px 39px 24px;
    }
    &__inner {
        max-height: 480px;
        .mCSB_container {
            margin-right: 0;
        }
        .mCSB_scrollTools {
            right: 6px;
        }
        .mCSB_scrollTools .mCSB_draggerRail {
            width: 3px;
            background: #E3E8F0;
            border-radius: 0;
        }
        .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
            width: 3px;
            background: #1EAEFF;
            border-radius: 0;
        }
        
        
    }
    &__item {
        padding: 0 60px;
        margin-bottom: 30px;
        .checkout-cart-item__title {
            margin-bottom: 8px;
        }
        &-option {
            position: relative;
            .services-item__label {
                position: absolute;
                left: 0;
                top: -23px;
                line-height: 10px;
            }
            @include response(767px) {
                margin: 35px 0 0;
            }
        }
        .quantity {
            margin-right: 60px;
            @include response(767px) {
                margin-right: 0;
            }
        }
        &-total {
            line-height: 30px;
            font-weight: 800;
            color: #36434C;
        }
        @include response(767px) {
            padding: 0 15px;
            flex-wrap: wrap;
            justify-content: space-around;
            .options-row {
                flex-wrap: wrap;
                justify-content: space-around;
                & > * {
                    width: 48%;
                }
            }
            .checkout-cart-item__thumb {
                margin: 0 0 30px;
            }
        }
    }
    &__price {
        margin-right: 10px;
        padding: 0 12px;
        height: 30px;
        line-height: 30px;
        color: #6E889B;
        font-size: 12px;
        font-weight: 800;
        background: #EFF3F8;
        border-radius: 5px;
        @include response(767px) {
            margin: 0;
            text-align: center;
        }
    }
    .package-btn {
        height: 30px;
        margin-right: 50px; 
        @include response(767px) {
            margin: 0;
        }
    }
    &__total {
        padding: 32px 60px;
        background: #F7FCFF;
        .continue-btn {
            background: #fff;
            height: 42px;
            line-height: 40px;
            width: 230px;
            @include desktop-hover {
                background: $blue;
            }
            @include response(767px) {
                margin-bottom: 15px;
            }
        }
        &-price {
            flex-shrink: 0;
            padding: 0 14px;
            height: 42px;
            line-height: 42px;
            background: #20A3EC;
            color: $white;
            font-weight: 800;
            border-radius: 5px 0 0 5px;
        }
        .checkout-btn {
            height: 42px;
            line-height: 40px;
            border-radius: 0 5px 5px 0;
            padding: 0 35px;
            font-size: 14px;
        }
        @include response(767px) {
            padding: 25px 15px;
            flex-wrap: wrap;
            justify-content: space-around;
        }
    }
}
.order-status {
    max-width: 382px;
    padding: 40px 15px 25px 35px;
    .contacts-form__title {
        margin-left: -35px;
        margin-right: -15px;
        padding: 0 35px 25px;
        flex-direction: column;
    }
    .pop-up__caption {
        margin-bottom: 0;
        span {
            color: #272727;
        }
    }
    &__title {
        font-size: 16px;
        font-weight: 800;
        color: $blue;
        margin: 10px 0 0;
    }
    &-item {
        position: relative;
        margin-bottom: 25px;
        &::before {
            content: '';
            width: 1px;
            background: #C7D3D9;
            height: 123%;
            left: -18px;
            top: 37%;
            position: absolute;
        }
        &:first-child::before {
            top: 55%;
        }
        &:last-child::before {
            top: auto;
            bottom: 46%;
        }
        &__date {
            font-size: 10px;
            font-weight: 700;
            color: #41484C;
            margin-bottom: 20px;
        }
        &__inner {
            align-items: flex-start;
            padding: 15px;
            background: #F7FCFF;
            border-bottom: 1px dashed #D0E1EB;
            position: relative;
            &::before {
                content: '';
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background: #C7D3D9;
                position: absolute;
                top: 30%;
                left: -20px;
            }
        }
        &__time {
            font-size: 12px;
            font-weight: 900;
            color: #41484C;
            margin-right: 8px;
        }
        &__label {
            font-size: 12px;
            font-weight: 600;
            color: #41484C;
            margin-bottom: 3px;
        }
        &__content {
            font-size: 14px;
            font-weight: 800;
            color: #53BC4F;
        }
        &.done {
            &::before {
                background: #53BC4F;
            }
            .order-status-item__time {
                font-weight: 500;
                color: #9AA2AD;
            }
            .order-status-item__inner {
                background: #EEF8ED;
                &::before {
                    background: #53BC4F;
                }
            }
            .order-status-item__content {
                color: #90A4B3;
                font-weight: 600;
            }
        }
    }
}