.account {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    &-page {
        &.account-partner {
            .page-title {
                svg {
                    display: block;
                    margin: 20px auto 0;
                }
                span {
                    position: relative;
                    display: inline-block;
                }
                span::before {
                    content: 'S';
                    position: absolute;
                    left: -45px;
                    top: 50%;
                    transform: translateY(-59%);
                    width: 36px;
                    height: 36px;
                    font-size: 20px;
                    font-weight: 700;
                    border-radius: 50%;
                    line-height: 39px;
                    text-align: center;
                    background: #0085E8;
                    color: #fff;
                }
            }
            .account {
                @include response(992px) {
                    flex-wrap: wrap;
                }
            }
            .account-orders__sort {
                @include response(992px) {
                    overflow-x: auto;
                }
            }
            .account-orders {
                min-width: 795px;
            }
            .account-inner {
                max-width: 100%;
                @include response(992px) {
                    overflow-x: auto;
                }
            }
            .account-sidebar {
                @include response(992px) {
                    margin: 0 0 30px;
                }
            }
        }
    }
    &-sidebar {
        width: 301px;
        flex-shrink: 0;
        margin-right: 46px;
        @include response(767px) {
            margin: 0 0 40px;
        }
    }
    &-nav {
        padding: 0;
        margin: 0;
        list-style: none;
        border: 1px solid #E5EDF8;
        margin-bottom: 25px;
        a {
            position: relative;
            height: 58px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 24px;
            font-size: 14px;
            font-weight: 800;
            color: #272727;
            text-decoration: none;
            border-bottom: 1px solid #E5EDF8;
            transition: .3s ease-in-out;
            &::before {
                content: '';
                position: absolute;
                left: -1px;
                top: 0;
                border-right: 3px solid $blue;
                height: 0;
                transition: .3s ease-in-out;
            }
            &::after {
                content: '';
                width: 22px;
                height: 22px;
                border: none;
                border-radius: 50%;
                background-color: transparent;
                background-image: url(../images/arrow-d.svg);
                background-repeat: no-repeat;
                background-position: center;
                margin: 0;
                transform: rotate(-90deg);
                transition: .3s ease-in-out;
            }
            &.active {
                background: #F5FBFF;
                &::before {
                    height: 100%;
                }
                &::after {
                    background-color: rgba(136, 216, 250, 0.18);
                }
            }
            @include desktop-hover {
                background: #F5FBFF;
            }
        }
    }
    &-log-out {
        width: 170px;
        height: 34px;
        line-height: 32px;
        display: block;
        margin: 0 auto; 
    }
    &-inner {
        width: 100%;
        &__title {
            font-size: 18px;
            font-weight: 800;
            color: #36434C;
            margin-bottom: 30px;
        }
    }
    &-data {
        display: flex;
        justify-content: space-between;
        &__thumb {
            @include response(992px) {
                display: none;
            }
        }
    }
    &-edit {
        width: 335px;
    }
    &-orders {
        .tabs-list {
            margin-bottom: 15px;
        }
        &__sort {
            margin-bottom: 15px;
        }
    }
    @include response(767px) {
        flex-wrap: wrap;
    }
}
.orders__opener {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background: #88D8FA;
    flex-shrink: 0;
    transition: .3s ease-in-out;
    transform: rotate(-90deg);
    svg {
        height: 10px;
        width: 6px;
        fill: #fff;
    }
}
.orders-grid {
    border: 1px solid #E5EDF8;
    &__row {
        display: flex;
        cursor: pointer;
    }
    &__cell {
        height: 67px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0 15px;
        width: 112px;
        flex-shrink: 0;
        font-size: 12px;
        font-weight: 700;
        color: #515A66;
        border-right: 1px solid #E5EDF8;
        border-bottom: 1px solid #E5EDF8;
        &.title {
            width: 100%;
            flex-shrink: 1;
            font-size: 14px;
            font-weight: 800;
            color: #272727;
        }
        &.details {
            width: 75px;
        }
    }
    &__heading {
        background: #F5F7FA;
        .orders-grid__cell {
            color: #9AA2AD;
            font-size: 12px;
            font-weight: 500;
            height: 40px;
        }
    }
}
.status-item {
    height: 24px;
    line-height: 24px;
    color: #fff;
    font-size: 10px;
    font-weight: 700;
    padding: 0 25px;
    border-radius: 46px;
    transition: .3s ease-in-out;
    &.used {
        background: #F3C028;
    }
    &.paved {
        background: #9BC753;
    }
}
.orders-item {
    border-bottom: 1px solid #E5EDF8;
    &__inner {
        padding-left: 35px;
        padding-top: 35px;
    }
    .services-item {
        padding: 0;
        border: none;
    }
    .checkout-cart-item__info,
    .services-item__info {
        margin: 0;
    }
    .services-item__info {
        padding-right: 50px;
    }
    &.active {
        .orders-grid__row {
            background: #F5FBFF;
        }
        .orders__opener {
            transform: rotate(90deg);
            background: rgba(#88D8FA, .18);
            svg {
                fill: #6E889B;
            }
        }
    }
}
.date-sort {
    position: relative;
    margin-right: 15px;
    input {
        background: transparent;
        padding-left: 5px;
        padding-right: 20px;
        border: none;
        font-size: 12px;
        line-height: 22px;
        max-width: 100px;
        font-weight: 700;
        font-family: 'Gothic A1', sans-serif;
        color: #515A66;
        cursor: pointer;
        outline: none;
    }
    svg {
        width: 13px;
        height: 13px;
        position: absolute;
        top: 5px;
        right: 5px;
        cursor: pointer;
        pointer-events: none;
    }
}
.datepickers-container {
    .datepicker {
        width: 165px;
    }
    .datepicker--pointer {
        display: none;
    }
    .datepicker--nav-action {
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background-image: url(../images/arrow-d.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-color: rgba(#025492, .18);
        background-size: 7px 10px;
        svg {
            display: none;
        }
        &[data-action="prev"] {
            transform: rotate(90deg);
        }
        &[data-action="next"] {
            transform: rotate(-90deg);
        }
    }
    .datepicker--nav {
        background: #D1F1FF;
        align-items: flex-end;
    }
    .datepicker--nav-title {
        font-size: 14px;
        font-weight: 700;
        color: #1EAEFF;
        display: block;
        text-align: center;
        span {
            display: block;
            font-size: 10px;
            color: #000;
            margin-bottom: 10px;
        }
    }
    .datepicker--cell {
        font-size: 10px;
        font-weight: 700;
        color: #000;
        &.-other-month- {
            opacity: 0;
        }
    }
    .datepicker--days-names {
        display: none;
    }
}