.contacts {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 70px;
    &-sidebar {
        width: 416px;
        margin-right: 60px;
        flex-shrink: 0;
        max-width: 100%;
        background: rgba(#EAF8FE, .35);
        padding: 25px 0;
        @include response(992px) {
            margin: 0;
            order: 10;
        }
    }
    &-inner {
        width: 100%;
        padding: 28px 0;
        .socials {
            padding-left: 32px;
            @include response(992px) {
                padding: 0;
                justify-content: center;
            }
        }
        @include response(992px) {
            margin-bottom: 50px;
        }
    }
    &-title {
        font-size: 22px;
        font-weight: 800;
        color: #36434C;
        padding-bottom: 20px;
        border-bottom: 1px dotted #C4D8E3;
        margin-bottom: 50px;
        @include response(992px) {
            text-align: center;
            margin-bottom: 30px;
        }
    }
    &-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        max-width: 467px;
        &__item {
            width: 205px;
            margin-bottom: 60px;
            &-inner {
                padding-left: 38px;
            }
            &-title {
                display: flex;
                align-items: center;
                font-size: 16px;
                font-weight: 800;
                text-transform: uppercase;
                color: #272727;
                margin-bottom: 20px;
            }
            &-thumb {
                width: 23px;
                height: 23px;
                display: flex;
                align-items: center;
                justify-content: center;
                background: #9DD3EC;
                border-radius: 50%;
                margin-right: 15px;
                svg {
                    width: 11px;
                    height: 11px;
                    fill: $white;
                }
            }
            &-line {
                display: flex;
                align-items: center;
                font-size: 14px;
                font-weight: 700;
                color: #515A66;
                text-decoration: none;
                margin-bottom: 6px;
                &.mail {
                    text-decoration: underline;
                }
                span {
                    margin-left: 15px;
                    font-size: 12px;
                    font-weight: 400;
                    color: #818C9A;
                }
                .off {
                    margin-left: 15px;
                    font-size: 12px;
                    font-weight: 400;
                    color: #FF5A5A
                }
            }
            .address {
                font-size: 14px;
                line-height: 22px;
                font-weight: 400;
                color: #9AA2AD;
            }
            @include response(992px) {
                margin-bottom: 30px;
            }
        }
        @include response(992px) {
           margin: 0 auto;
        }
    }
    &-form {
        &__title {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 16px;
            font-weight: 800;
            color: #36434C;
            padding-bottom: 24px;
            border-bottom: 1px dashed #D0E1EB;
            margin-bottom: 27px;
            svg {
                width: 24px;
                height: 24px;
                margin-right: 17px;
            }
        }
        &__inner {
            padding: 0 40px;
            @include response(767px) {
               padding: 0 15px;
            }
        }
        &__caption {
            text-align: center;
            font-size: 12px;
            line-height: 15px;
            color: #7A8087;
            margin-bottom: 19px;
        }
        .form-field,
        .form-area {
            background: $white;
        }
        .btn {
            display: block;
            margin: 0 auto;
            max-width: 174px;
            font-size: 14px;
            font-weight: 700;
        }
    }
    @include response(992px) {
        flex-wrap: wrap;
        justify-content: space-around;
    }
}