.home {
    &-page {
        .page-inner {
            padding-left: 313px;
            @include response(992px) {
                padding-left: 15px;
            }
        }
        .decor-item-1 {
            top: 11%;
            left: 11%;
        }
        .decor-item-2 {
            top: 20%;
            left: 4%;
        }
        .decor-item-3 {
            top: 35%;
            left: 10%;
        }
        .decor-item-4 {
            top: 8%;
            right: 6%;
        }
        .decor-item-5 {
            top: 15%;
            right: 7%;
        }
        .decor-item-6 {
            top: 25%;
            right: 9%;
        }
    }
    .catalog-btn {
        pointer-events: none;
        @include response(992px) {
            pointer-events: all;
        }
    }
    .catalog-list {
        display: block;
        @include response(992px) {
            display: none;
        }
    }
    @include response(767px) {
        .products-list .slick-slide {
            margin-right: 15px;
        }
    }
}
.heading-slider {
    .slick-arrow {
        width: 33px;
        height: 33px;
        border-radius: 50%;
        text-align: center;
        z-index: 3;
        background: $white;
        box-shadow: 0 2px 10px 0 rgba(206, 212, 216, 0.5);
        transition: .3s ease-in-out;
        &::before {
            content: '';
            display: inline-block;
            width: 7px;
            height: 12px;
            background-image: url('../images/arrow.svg');
            background-size: cover;
            opacity: 1;
        }
        @include desktop-hover {
            box-shadow: 0 2px 18px 0 rgba($black, 0.2);
        }
    }
    .slick-prev {
        left: 40px;
        &::before {
            margin-right: 3px;
        }
    }
    .slick-next {
        right: 40px;
        &::before {
            transform: scale(-1, 1);
            margin-left: 3px;
        }
    }
    .slick-dots {
        bottom: 17px;
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: center;
        li {
            width: 11px;
            height: 11px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid transparent;
            transition: .3s ease-in-out;
            &.slick-active {
                border-color: $white;
            }
        }
        button {
            width: 7px;
            height: 7px;
            border-radius: 50%;
            background: $white;
            padding: 0;
            &::before {
                display: none;
            }
        }
    }
}