.checkout {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0 auto 50px;
    max-width: 1000px;
    .form-label {
        font-weight: 500;
        font-size: 10px;
        color: #9AA2AD;
    }
    &-inner {
        width: 560px;
        margin-right: 48px;
        max-width: 100%;
        counter-reset: count;
        @include response(768px) {
            width: 400px;
            margin-right: 30px;
        }
        @include response(767px) {
            width: 100%;
            margin: 0 0 30px;
        }
    }
    &-item {
        counter-increment: count;
        border: 1px solid #E5EDF8;
        margin-bottom: 30px;
        &__title {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;
            border-bottom: 1px solid #E5EDF8;
            font-weight: 800;
            font-size: 18px;
            color: #515A66;
            &::before {
                content: counter(count);
                width: 23px;
                height: 23px;
                background: #0085E8;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #fff;
                font-size: 12px;
                font-weight: 500;
                margin-right: 11px;
            }
        }
        .tab {
            padding: 0 75px 55px;
            @include response(767px) {
                padding: 0 20px 35px;
            }
        }
        .custom-checkbox {
            justify-content: center;
            margin-bottom: 25px;
        }
        &.disabled {
            border-color: transparent;
            background: #F5F7FA;
            .checkout-item__title {
                color: #9AA2AD;
                border-color: transparent;
                &::before {
                    background: #9AA2AD;
                }
            }
        }
        &.done {
            height: 64px;
            overflow: hidden;
            .checkout-item__title {
                background: #F4F7F9;
                &::before {
                    content: '';
                    background-color: #37C59D;
                    background-image: url('../images/checked.svg');
                    background-size: 10px 8px;
                    background-position: center;
                    background-repeat: no-repeat;
                }
            }
        }
    }
    &-cart {
        width: 392px;
        background: rgba(#EAF8FE, .35);
        border-top: 3px solid $blue;
        padding-bottom: 35px;
        border-radius: 0 0 6px 6px;
        &-item {
            padding: 25px 20px;
            border-bottom: 1px dashed #D6DFEB;
            margin-bottom: 30px;
            &__title {
                display: block;
                font-size: 16px;
                font-weight: 800;
                text-decoration: none;
                color: #36434C;
                margin-bottom: 15px;
            }
            &__thumb {
                flex-shrink: 0;
                margin-right: 25px;
            }
            .row {
                align-items: flex-start;
            }
            &__info {
                display: flex;
                padding-top: 13px;
                border-top: 1px solid #E5EDF8;
                margin-bottom: 30px;
            }
            &__remove {
                width: 165px;
                height: 36px;
                background: #fff;
                border: 1px solid #E5EDF8;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #9AA2AD;
                cursor: pointer;
                border-radius: 4px;
                font-size: 12px;
                font-weight: 700;
                transition: .3s ease-in-out;
                svg {
                    width: 8px;
                    height: 8px;
                    margin-right: 9px;
                }
                @include desktop-hover {
                    background: rgba(#9AA2AD, .1);
                }
            }
        }
        .price-total {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding: 0 30px;
            .services-price__label {
                margin: 0 7px 0 0;
            }
        }
        @include response(768px) {
            width: 340px;
        }
        @include response(767px) {
            width: 100%;
        }
    }
    &-btn {
        width: 100%;
    }
    @include response(767px) {
        flex-wrap: wrap;
        justify-content: space-around;
    }
}
.use-date {
    margin-left: 27px;
    font-weight: 700;
    color: #515A66;
    .services-item__label {
        margin-bottom: 9px;
        font-size: 12px;
        
    }
}
.payment-method {
    padding: 40px 0;
    &__inner {
        max-width: 391px;
        margin: 0 auto;
    }
    &__title {
        font-weight: 800;
        color: #515A66;
        margin-bottom: 20px;
    }
    & + .payment-method {
        border-top: 1px dashed #D6DFEB;
    }
    .custom-checkbox {
        justify-content: flex-start;
        &__label {
            font-size: 16px;
            font-weight: 700;
        }
        &__box {
            width: 18px;
            height: 18px;
            &::before {
                width: 12px;
                height: 12px;
            }
        }
    }
    .select {
        background: #F7FCFF;
        border-radius: 5px;
        margin-bottom: 10px;
        padding: 15px;
        padding-right: 50px;
        border: 1px solid #DEECF4;
        &__current {
            font-weight: 500;
            color: #9AA2AD;
        }
        &::after {
            right: 20px;
            background-color: transparent;
        }
    }
}
.credit-card {
        padding: 30px 25px 25px;
        background: #F5FBFF;
        .form-field {
            background: #fff;
        }
    &__number {
        display: flex;
        justify-content: space-between;
        margin-bottom: 35px;
    }
    &__field {
        width: 78px;
    }
}
.package-btn {
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(136, 216, 250, 0.18);
    width: 165px;
    border-radius: 4px;
    color: #36434C;
    font-size: 12px;
    font-weight: 700;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
        width: 14px;
        height: 14px;
        margin-right: 11px;
    }
    @include desktop-hover {
        background: rgba(136, 216, 250, 0.28);
    }
}
.date-group {
    display: flex;
}
.date-field {
    position: relative;
    margin-right: 12px;
    &__nav {
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        svg {
            width: 7px;
            height: 4px;
        }
    }
    &__btn {
        padding: 4px;
        line-height: 10px;
        cursor: pointer;
        &.dec {
            transform: scale(1, -1);
        }
    }
    &.day,
    &.month {
        width: 68px;
    }
    &.year {
        width: 106px;
    }
}
.time-group {
    display: flex;
    align-items: center;
    .separator {
        width: 8px;
        border-top: 1px solid #515A66;
        margin: 0 15px;
    }
    .time-field {
        position: relative;
        width: 106px;
        .form-field {
            padding: 0 15px;
        }
        &__inner.form-field {
            display: flex;
            align-items: center;
            justify-content: center;
            padding-right: 25px;
            input {
                background: transparent;
                border: none;
                width: 20px;
                text-align: center;
                font-family: 'Gothic A1', sans-serif;
                font-size: 14px;
                font-weight: 700;
                color: #515A66;
                outline: none;
            }
        }
    }
}